  color: #000;
}

.text-page table td {
  padding: 15px 23px;
  font-weight: 300;
  vertical-align: middle;
}

.text-page table th {
  padding: 37px 23px;
  font-weight: 300;
  vertical-align: middle;
}

.text-page table[border="1"] td, .text-page table[border="1"] th {
  border: 1px solid #e4e8ea;
}

.text-page ul, .text-page ol {
  max-width: 650px;
  margin-top: 20px !important;
}

.text-page li {
  position: relative;
}

.text-page li:before {
  display: inline-block;
}

.text-page li ul, .text-page li ol {
  margin-top: 0 !important;
  margin-bottom: 20px;
}

.text-page li li {
  padding-left: 20px;
}

.text-page li li:not(:first-child) {
  margin-top: 0;
}

.text-page ul {
  margin-bottom: 30px;
}

.text-page ul li:before {
  content: "";
  background: #000;
  width: 12px;
  height: 1px;
  vertical-align: middle;
  margin: 0 14px 4px 0;
}

.text-page ul ul li:before {
  content: "-";
  height: auto;
  width: auto;
  background: none;
  margin: 0 5px 5px 0;
}

.text-page ol {
  counter-reset: i;
}

.text-page ol li {
  counter-increment: i;
}

.text-page ol li:before {
  content: counter(i) ".";
  margin-right: 4px;
}

.text-page dt, .text-page dd {
  display: inline;
  font-style: italic;
}

.text-page blockquote {
  position: relative;
  display: inline-block;
  padding: 4px 0 7px 17px;
}

.text-page .btn {
  display: inline-block;
  margin-top: 35px;
}

.checkbox__input {
  display: none;
}

.checkbox__input:checked + label:after {
  opacity: 1;
}

.checkbox__label {
  position: relative;
  padding-left: 40px;
  cursor: pointer;
  min-height: 24px;
  display: flex;
  align-items: center;
}

.checkbox__label:before, .checkbox__label:after {
  position: absolute;
  content: "";
}

.checkbox__label:before {
  background: #fff;
  height: 23px;
  width: 23px;
  left: 0;
  top: 0px;
}

.checkbox__label:after {
  transition: opacity .3s ease;
  height: 6px;
  width: 11px;
  left: 6px;
  top: 9px;
  background: url("../img/ico-checbox-check.png") no-repeat;
  opacity: 0;
}

.radio__input {
  display: none;
}

.radio__input:checked + label:after {
  opacity: 1;
}

.radio__label {
  padding-left: 30px;
  min-height: 15px;
  position: relative;
  display: block;
  cursor: pointer;
}

.radio__label:before, .radio__label:after {
  content: "";
  position: absolute;
  display: block;
  border-radius: 50%;
}

.radio__label:before {
  border: 1px solid #e4e8ea;
  height: 15px;
  width: 15px;
  left: 0;
  top: 0;
}

.radio__label:after {
  top: 4px;
  left: 4px;
  background: #5291da;
  height: 7px;
  width: 7px;
  transition: opacity .3s ease;
  opacity: 0;
}

