@media screen and (max-width: 1000px) {
  .burger {
    position: relative;
    z-index: 1002;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: content-box;
    width: 30px;
    height: 30px;
    padding: 15px;
    background: 0 0;
    border: 0;
    user-select: none;
    margin-left: -15px;
    margin-right: -15px;
    cursor: pointer;
    border-radius: 50%;
    transition: border-radius 1s ease, background 1s ease;
  }
  .burger:before {
    display: none;
  }
  .burger span {
    position: relative;
    display: inline-block;
    width: 30px;
    height: 3px;
    vertical-align: bottom;
    background: #5291da;
    transition: background .2s .4s;
  }
  .burger span:before, .burger span:after {
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    height: 3px;
    background: #5291da;
    transform-origin: 50% 50%;
    transition: top .3s .3s,transform .3s;
  }
  .burger span:before {
    border-radius: 0;
    transform: none;
    top: 8px;
  }
  .burger span:after {
    top: -8px;
  }
  .mobile-menu--open #content {
    transform: translate3d(-250px, 0, 0);
  }
  .mobile-menu--open .burger > span {
    background: 0 0;
  }
  .mobile-menu--open .burger > span:before {
    transform: rotate3d(0, 0, 1, 45deg);
    top: 0;
    transition: top .3s,transform .3s .5s;
  }
  .mobile-menu--open .burger > span:after {
    transform: rotate3d(0, 0, 1, -45deg);
    top: 0;
    transition: top .3s,transform .3s .5s;
  }
  .mobile-menu--open .mobile-menu {
    opacity: 1;
    pointer-events: all;
  }
  .mobile-menu--open .burger-shadow {
    transform: translate(-50%, -50%) scale(1);
  }
  .mobile-menu {
    display: block;
    position: absolute;
    right: 0;
    top: 0;
    width: 250px;
    height: 100%;
    z-index: 1;
    padding: 0;
    transition: transform .3s ease;
  }
  .mobile-menu .main-nav {
    display: block;
  }
  .mobile-menu .main-nav__list {
    display: flex;
    flex-direction: column;
  }
  .mobile-menu .main-nav__link {
    display: block;
    color: #000;
    font-size: 1rem;
    padding: 15px;
    text-align: left;
  }
  .mobile-menu .main-nav__link:hover {
    background: transparent;
  }
  .mobile-menu .main-nav__item {
    position: static;
    border-bottom: 1px solid rgba(0, 0, 0, 0.3);
  }
  .mobile-menu .main-nav__item.active {
    background: transparent;
  }
  .mobile-menu .sub-menu {
    position: absolute;
    top: 0;
    right: 0;
    width: 250px;
    height: 100%;
    background: #fff;
    transform: translate3d(100%, 0, 0);
    display: block;
    transition: transform .3s ease;
    box-shadow: none;
  }
  .mobile-menu .sub-menu__link {
    color: #000;
    padding: 15px;
    font-size: 1rem;
    white-space: normal;
    border-bottom: 1px solid rgba(0, 0, 0, 0.3);
  }
  .mobile-menu .sub-menu .main-nav__link {
    border-bottom: 1px solid rgba(0, 0, 0, 0.3);
  }
  .mobile-menu .sub-menu .main-nav__link:after {
    display: none;
  }
  .back {
    display: flex;
    align-items: center;
    position: relative;
    padding: 15px;
    font-size: 1rem;
    cursor: pointer;
    border-bottom: 1px solid rgba(0, 0, 0, 0.3);
  }
  .back:before {
    content: "";
    height: 6px;
    width: 6px;
    border-top: 1px solid #000;
    border-left: 1px solid #000;
    transform: rotate(-45deg);
    display: inline-block;
    margin-right: 10px;
  }
  .js__has-submenu .main-nav__link {
    position: relative;
  }
  .js__has-submenu .main-nav__link:after {
    content: "";
    display: block;
    position: absolute;
    right: 15px;
    top: calc(50% - 3px);
    height: 6px;
    width: 6px;
    border-top: 1px solid #000;
    border-right: 1px solid #000;
    transform: rotate(45deg);
  }
  .js__open--submenu .sub-menu {
    transform: translate3d(0, 0, 0);
  }
  .head-top .questions {
    display: none;
  }
  .main-nav, .sids-right, .head-top__location, .search-btn {
