@media screen and (max-width: 1000px) {

	.burger {
		position: relative;
		z-index: 1002;
		display: flex;
		justify-content: center;
		align-items: center;
		box-sizing: content-box;
		width: 30px;
		height: 30px;
		padding: 15px;
		background: 0 0;
		border: 0;
		user-select: none;
		margin-left: -15px;
		margin-right: -15px;
		cursor: pointer;
		border-radius: 50%;
		transition: border-radius 1s ease, background 1s ease;
		&:before {
			display: none; }

		span {
			position: relative;
			display: inline-block;
			width: 30px;
			height: 3px;
			vertical-align: bottom;
			background: $main-color;
			transition: background .2s .4s;

			&:before, &:after {
				content: "";
				position: absolute;
				left: 0;
				width: 100%;
				height: 3px;
				background: $main-color;
				transform-origin: 50% 50%;
				transition: top .3s .3s,transform .3s; }

			&:before {
				border-radius: 0;
				transform: none;
				top: 8px; }

			&:after {

				top: -8px; } } }

	.mobile-menu--open {

		// .main-slider__cont, .therapy, .about, .testemonials, .footer, .recommendation
		// 	display: none

		#content {
			transform: translate3d(-250px,0,0); }


		.burger {

			> span {
				background: 0 0; } }
		.burger {
			> span {
				&:before {
					transform: rotate3d(0,0,1,45deg);
					top: 0;
					transition: top .3s,transform .3s .5s; } } }
		.burger {
			> span {
				&:after {
					transform: rotate3d(0,0,1,-45deg);
					top: 0;
					transition: top .3s,transform .3s .5s; } } }

		.mobile-menu {
			opacity: 1;
			pointer-events: all; }


		.burger-shadow {
			transform: translate(-50%, -50%) scale(1); } }



	.mobile-menu {
		display: block;
		position: absolute;
		right: 0;
		top: 0;
		width: 250px;
		height: 100%;
		z-index: 1;
		padding: 0;
		transition: transform .3s ease;

		.main-nav {
			display: block;

			&__list {
				display: flex;
				flex-direction: column; }
			&__link {
				display: block;
				color: #000;
				font-size: rem(16);
				padding: 15px;
				text-align: left;
				&:hover {
					background: transparent; } }

			&__item {
				position: static;
				border-bottom: 1px solid rgba(#000, .3);


				&.active {
					background: transparent; } } }


		.sub-menu {
			position: absolute;
			top: 0;
			right: 0;
			width: 250px;
			height: 100%;
			background: #fff;
			transform: translate3d(100%,0,0);
			display: block;
			transition: transform .3s ease;
			box-shadow: none;

			&__link {
				color: #000;
				padding: 15px;
				font-size: 1rem;
				white-space: normal;
				border-bottom: 1px solid rgba(#000, .3); }

			.main-nav__link {
				border-bottom: 1px solid rgba(#000, .3);

				&:after {
					display: none; } } } }



	.back {
		display: flex;
		align-items: center;
		position: relative;
		padding: 15px;
		font-size: 1rem;
		cursor: pointer;
		border-bottom: 1px solid rgba(#000, .3);

		&:before {
			content: "";
			@include size(6px);
			border: {
				top: 1px solid #000;
				left: 1px solid #000; }
			transform: rotate(-45deg);
			display: inline-block;
			margin-right: 10px; } }






	.js__has-submenu {
		.main-nav__link {
			position: relative;

			&:after {
				content: "";
				display: block;
				position: absolute;
				right: 15px;
				top: calc(50% - 3px);
				@include size(6px);
				border: {
					top: 1px solid #000;
					right: 1px solid #000; }
				transform: rotate(45deg); } } }


	.js__open--submenu {
